import {defineStore} from "pinia";


export const adminStore = defineStore('adminStore', {
    state: (() => {
        return {
            mysqlTable: [
                {
                    name: "mws_metrics",
                    status: 0,
                    size: 0,
                },
                {
                    name: "mws_metrics_units",
                    status: 0,
                    size: 0,
                },
                {
                    name: "mws_abonent",
                    status: 0,
                    size: 0,
                },
                {
                    name: "mws_abonent_status",
                    status: 0,
                    size: 0,
                },
                {
                    name: "mws_settings",
                    status: 0,
                    size: 0,
                }
            ],
            metric: [],
            proactiveStatus: [],
            dealCategory: [],
            dealFields: [],
            smartLists: [],
            smartFields: [],
            listAll: [],
            listCity: [],
            settings: {
                categoryMain: null,
                categoryKC: null,
                categoryAO: null,
                smartPersonalAccount:null,
                listCity: null,
                sityDay: [],
                defaultDay: 0,
                dealField:{
                    engineer:null,
                    technicist:null,
                },
                smartField:{
                    personalAccount:null,
                    address:null,
                    city:null,
                    asigned:null,
                }

            }
        }
    }),
    actions: {
        async checkTable() {
            const table = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.checktable', {table: this.mysqlTable}, res => {
                    resolve(res.data())
                })
            })
            this.mysqlTable = table
        },
        async getDealCategory() {

            const entity = await new Promise((resolve) => {
                BX.rest.callMethod(
                    "crm.dealcategory.list",
                    {},
                    result => {
                        if (result.error())
                            resolve(result.error());
                        else
                            resolve(result.data());
                    }
                );
            })


            this.dealCategory = entity

        },
        async getDealFields() {
            const fields = await new Promise((resolve) => {
                BX.rest.callMethod("crm.deal.fields", {}, res => {
                    resolve(res.data())
                })
            })
            for (var key in fields) {
                this.dealFields.push({
                    'ID':key,
                    'NAME':fields[key].title
                })

            }


        },
        async getSmartFields() {
            if (this.settings.smartPersonalAccount) {
                const fields = await new Promise((resolve) => {
                    BX.rest.callMethod("mwsproactiverest.getUserFields", {'ENTITY_ID':this.settings.smartPersonalAccount.ID}, res => {
                        resolve(res.data())
                    });
                })


                this.smartFields = fields;
            }
        },
        async getSmarts() {
            const smarts = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.smart.getSmarts', {}, res => {
                    resolve(res.data())
                })
            })
            this.smartLists = smarts
        },
        async getLists() {
            const lists = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.getLists', {}, res => {
                    resolve(res.data())
                })
            })
            this.listAll = lists
        },
        async getCities() {
            if (this.settings.listCity) {
                const city = await new Promise((resolve) => {
                    BX.rest.callMethod('mwsproactiverest.list.getElements',
                        {ID: this.settings.listCity}, res => {
                            resolve(res.data())
                        })
                })
                this.listCity = city
            }

        },
        async setMetric(metric) {
            const metricAdd = new Promise((resolve, reject) => {
                BX.rest.callMethod("mwsproactiverest.metric.add", {metric}, res => {
                    if (res.error()) {
                        reject(res.error())
                    } else {
                        resolve(res.data())
                    }
                })
            })
            return metricAdd
        },
        async getMetric() {

            const metrics = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.metric.getList', {
                    order: [],
                    filter: [],
                    select: ['*'],
                }, res => {
                    resolve(res.data())
                })
            })
            metrics.forEach(elem => {
                this.metric.push({
                    id: elem.ID,
                    metricName: elem.NAME,
                    metricASUO: elem.METRIC_ASUO,
                    metricMeaning: elem.MEANING,
                    metricStandart: elem.STANDART,
                    metricDescription: elem.DESCRIPTION,
                    metricTests: elem.TESTS,
                })


            })


        },
        async updateMetric(elem) {
            const upd = await new Promise((resolve) => {
                BX.rest.callMethod(
                    "mwsproactiverest.metric.update",
                    {
                        ID: elem.id,
                        fields: {
                            "NAME": elem.metricName,
                            "METRIC_ASUO": elem.metricASUO,
                            "MEANING": elem.metricMeaning,
                            "STANDART": elem.metricStandart,
                            "DESCRIPTION": elem.metricDescription,
                            "TESTS": elem.metricTests
                        }
                    },
                    result => {
                        if (result.error())
                            resolve(result.error());
                        else
                            resolve(result.data());
                    }
                );
            })

            return upd;

        },
        async deleteMetric(elem) {
            const del = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.metric.delete', {ID: elem.id}, res => {
                    resolve(res.data())


                })
            })
            return del;

        },
        async getProactiveStatuses() {
            const statuses = await new Promise((resolve) => {
                BX.rest.callMethod('mwsproactiverest.abonentStatus.getList', {
                    order: [],
                    filter: [],
                    select: ['*'],
                }, res => {
                    resolve(res.data())
                })
            })
            this.proactiveStatus = statuses
        },


    },
    getters: {
        getTableCheck(state) {
            return state.mysqlTable
        },
        getTableMetric(state) {
            return state.metric
        },
        getStatus(state) {
            return state.proactiveStatus
        },
        getDdealCategory(state) {
            return state.dealCategory;
        },
        getSmartLists(state) {
            return state.smartLists;
        },
        getListAll(state) {
            return state.listAll;
        },
        getFieldsDeal(state){
            return state.dealFields
        },
        getFieldsSmart(state){
            return state.smartFields
        }

    }
})