<template>
  <div class="flex flex-wrap flex-column gap-3">
    <Panel toggleable collapsed="false" class="w-full">
      <template #header>
        Метрики <span> Всего {{ asr.metric.value.length }} метрик</span>
      </template>
      <div class="mb-3">
        <Button label="Добавить" @click="addDialogMetric = !addDialogMetric"/>

      </div>

      <DataTable
                :value="as.getTableMetric"
                 paginator
                 :rows="5"
                :rowsPerPageOptions="[5, 10, 20, 50]"
                 v-model:editingRows="editingRows"
                  v-model:selection="selectedProduct"
                editMode="row"
                dataKey="id"
                @row-edit-save="onRowEditSave"
      >
        <Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
        <Column field="id">
          <template #header>
            Ид
          </template>
          <template #body="slotProps">
            {{ slotProps.data.id }}
          </template>

        </Column>
        <Column field="metricName">
          <template #header>
            Метрика
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricName }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricASUO">
          <template #header>
            Ид метрики АСУО
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricASUO }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricMeaning">
          <template #header>
            Возможное значение
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricMeaning }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricStandart">
          <template #header>
            Норматив
          </template>
          <template #body="slotProps">
            {{ slotProps.data.metricStandart }}
          </template>
          <template #editor="{ data, field }">
            <InputText v-model="data[field]"/>
          </template>
        </Column>
        <Column field="metricDescription">
          <template #header>
            Описание
          </template>
          <template #body="slotProps">
            <div class="w-20rem word-wrap"
                 v-tooltip="{ value:slotProps.data.metricDescription, showDelay: 1000, hideDelay: 300 }">
              {{ slotProps.data.metricDescription }}
            </div>
          </template>
          <template #editor="{ data, field }">
            <Textarea v-model="data[field]" autoResize rows="5" cols="30"/>
          </template>
        </Column>
        <Column field="metricTests">
          <template #header>
            Регламент тестов
          </template>
          <template #body="slotProps">
            <div class="w-20rem word-wrap"
                 v-tooltip="{ value:slotProps.data.metricTests, showDelay: 1000, hideDelay: 300 }">
              {{ slotProps.data.metricTests }}
            </div>
          </template>
          <template #editor="{ data, field }">
            <Textarea v-model="data[field]" autoResize rows="5" cols="30"/>
          </template>
        </Column>
        <Column :rowEditor="true" style="width: 10%; min-width: 8rem" bodyStyle="text-align:center"></Column>
        <template #empty>
            <span class="align-content-center">
           В данный момент нет не одной метрики.
          </span>
        </template>

      </DataTable>

      <div class="mb-3"  v-if="selectedProduct.length > 0">
        <Button label="Удалить" @click="delDialogMetric = !delDialogMetric"/>

      </div>
    </Panel>

    <Panel header="Привязки к сушностям" toggleable collapsed="false" class="w-full">
      <div class="flex flex-nowrap flex-row gap-3 justify-content-start">

        <Card>
          <template #header>
          </template>
          <template #title>Сделки воронка</template>
          <template #subtitle></template>
          <template #content>
            <div class="flex flex-column gap-3">
            <Dropdown v-model="asr.settings.value.categoryMain" :options="as.getDdealCategory" optionLabel="NAME" placeholder="Выбирете воронку заявок " checkmark :highlightOnSelect="false" class="w-full" />
            <Dropdown v-model="asr.settings.value.categoryKC" :options="as.getDdealCategory" optionLabel="NAME" placeholder="Выбирете воронку КЦ " checkmark :highlightOnSelect="false" class="w-full" />
            <Dropdown v-model="asr.settings.value.categoryAO" :options="as.getDdealCategory" optionLabel="NAME" placeholder="Выбирете воронку AО " checkmark :highlightOnSelect="false" class="w-full" />
            </div>
            <div class="mt-3" v-if="as.getFieldsDeal">

              <div class="p-card-title">Связанные поля</div>


              <div class="field">
                <label for="lastname2">Инженер</label>
                <Dropdown v-model="asr.settings.value.dealField.engineer" :options="as.getFieldsDeal"  optionLabel="NAME" placeholder="Выбирете поле Инжинер" id="lastname2" class=" w-full"/>
              </div>

              <div class="field">
                <label for="lastname4">Техник</label>
                <Dropdown v-model="asr.settings.value.dealField.technicist" :options="as.getFieldsDeal"  optionLabel="NAME" placeholder="Выбирете поле Техник"  id="lastname4" class=" w-full"/>
              </div>

            </div>

          </template>

        </Card>
        <Card>
          <template #header>
          </template>
          <template #title>Лицевые счета (смарт процесс)</template>
          <template #subtitle></template>
          <template #content>
            <Dropdown v-model="asr.settings.value.smartPersonalAccount" :options="as.getSmartLists" @change="loadSmartFields" optionLabel="NAME" placeholder="Выбирете смарт процесс" checkmark :highlightOnSelect="false" class="w-full" />
            <div class="mt-3" v-if="as.getFieldsSmart">

              <div class="p-card-title" >Связанные поля</div>

              <div class="field">
                <label for="firstname1">Лицевой счет</label>
                <Dropdown id="firstname1"  v-model="asr.settings.value.smartField.personalAccount" :options="as.getFieldsSmart"  optionLabel="NAME" class="w-full"/>
              </div>
              <div class="field">
                <label for="lastname2">Адрес</label>
                <Dropdown id="lastname2" v-model="asr.settings.value.smartField.address"  :options="as.getFieldsSmart" optionLabel="NAME" class=" w-full"/>
              </div>
              <div class="field">
                <label for="lastname3">Участок связи или город</label>
                <Dropdown id="lastname3" v-model="asr.settings.value.smartField.city" :options="as.getFieldsSmart"  optionLabel="NAME" class=" w-full"/>
              </div>
              <div class="field">
                <label for="lastname4">Ответственный</label>
                <Dropdown id="lastname4" v-model="asr.settings.value.smartField.asigned" :options="as.getFieldsSmart"  optionLabel="NAME"  class=" w-full"/>
              </div>

            </div>
          </template>
        </Card>
        <Card class="w-30rem">
          <template #title>Город</template>
          <template #content>
            <Dropdown v-model="asr.settings.value.listCity" :options="as.getListAll" @change="saveChangeCity"  optionLabel="NAME" placeholder="Выбирете список" checkmark :highlightOnSelect="false" class="w-full" />
            <div class="mt-3">
            <div class="field">
              <label for="firstname1">Повторное обрашение значение в днях</label>
              <InputText v-model="asr.settings.value.defaultDay" id="firstname1 "class="w-full"/>
            </div>
              <DataTable :value="asr.settings.value.sityDay" size="small" key="ID" class="w-full">
                <Column field="NAME">
                  <template #header>Город</template>
                  <template #body="slotProps">{{slotProps.data.NAME}}</template>
                </Column>
                <Column field="DAY">
                  <template #header>Дни</template>
                </Column>
                <template #footer><div  class="flex justify-content-end" ><Button label="Добавить" @click="dialogCity = !dialogCity"/></div></template>
              </DataTable>
            </div>
          </template>
        </Card>

      </div>
      <Card class="w-full mt-3">
        <template #content>
      <div class="flex justify-content-end">
          <Button label="Сохранить"  @click="saveSettings()"/>
      </div>
        </template>
      </Card>

    </Panel>

    <Dialog v-model:visible="addDialogMetric" header="Добавить метрику" :style="{ width: '35rem' }" position="topright"
            :modal="true" :draggable="false">
      <span class="p-text-secondary block mb-5">Update your information.</span>
      <div class="flex flex-column  gap-3 mb-3">
        <label for="metricName" class="font-semibold ">Название метрики</label>
        <InputText id="metricName" v-model="metricName" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-3">
        <label for="metricName" class="font-semibold ">ID метрики АСУО</label>
        <InputText id="metricName" v-model="metricASUO" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column gap-3 mb-5">
        <label for="metricMeaning" class="font-semibold">Возможные значения</label>
        <InputText id="metricMeaning" v-model="metricMeaning" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metriStandart" class="font-semibold ">Стандарт</label>
        <InputText id="metricStandart" v-model="metricStandart" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metricDescription" class="font-semibold ">Описание метрики</label>
        <Textarea id="metricDescription" v-model="metricDescription" autoResize rows="5" cols="30"/>
      </div>
      <div class="flex flex-column  gap-3 mb-5">
        <label for="metricTests" class="font-semibold ">Регламент тестов</label>
        <Textarea id="metricTests" v-model="metricTests" autoResize rows="5" cols="30"/>
      </div>
      <div class="flex justify-content-end gap-2">
        <Button type="button" label="Отмена" severity="secondary" @click="addDialogMetric = !addDialogMetric"></Button>
        <Button type="button" label="Сохранить" @click="addDialogMetric = false, saveMetric()"></Button>
      </div>
    </Dialog>
    <Dialog v-model:visible="delDialogMetric" header="Удаление метрик" :style="{ width: '35rem' }" position="topright"  :modal="true" :draggable="false">
      <Card   class="w-20rem">
        <template #header>
        </template>
        <template #title>Удалить выбраные Метрики?</template>
        <template #subtitle>Удалятся только метрики данные из АСУО останутся в таблице</template>
        <template #content>


          <div class="flex flex-column">
      <div class="flex align-items-center justify-content-between h-3rem font-bold border-round m-2 p-2" v-for="table in selectedProduct" ><span>{{table.id}}</span> <span>{{table.metricName}}</span>  </div>
          </div>
        </template>
         <template #footer>
           <div class="flex justify-content-end gap-2">
             <Button type="button" label="Отмена" severity="secondary" @click="delDialogMetric = !delDialogMetric , selectedProduct = []"></Button>
             <Button type="button" label="Удалить" @click="delDialogMetric = false, delMetric()"></Button>
           </div>
         </template>
      </Card>
    </Dialog>
    <Dialog v-model:visible="dialogCity" header="Добавить повторяемость" :style="{width:'35rem'}" position="topright" :modal="true" :draggable="false">
      <div class="flex flex-column  gap-3 mb-3">
        <label for="metricName" class="font-semibold ">Город</label>
        <Dropdown id="metricName" v-model="cityNAME" :options="asr.listCity.value"  optionLabel="NAME" placeholder="Выбирете город" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex flex-column  gap-3 mb-3">
        <label for="metricName" class="font-semibold ">Повторяемость дни</label>
        <InputText id="metricName" v-model="cityDAY" class="flex-auto" autocomplete="off"/>
      </div>
      <div class="flex justify-content-end gap-2">
        <Button type="button" label="Отмена" severity="secondary" @click="dialogCity = !dialogCity"></Button>
        <Button type="button" label="Сохранить" @click="dialogCity = false, saveCity()"></Button>
      </div>
    </Dialog>

  </div>
</template>

<script setup>
import {onMounted, ref} from "vue";
import {adminStore} from "../../store/adminStore.js";
import {storeToRefs} from "pinia";
import {useToast} from 'primevue/usetoast';

const toast = useToast();
const as = adminStore()
const asr = storeToRefs(as)
const addDialogMetric = ref(false)
const delDialogMetric = ref(false)
const dialogCity = ref(false)

const metricName = ref('')
const metricASUO = ref('')
const metricMeaning = ref('')
const metricStandart = ref('')
const metricDescription = ref('')
const metricTests = ref('')
const editingRows = ref([]);
const selectedProduct = ref([]);
const selectedSmart = ref();
const cityNAME = ref();
const cityDAY = ref();
const  saveCity = async ()=>{
  asr.settings.value.sityDay.push({
    'ID': cityNAME.value.ID,
     'NAME': cityNAME.value.NAME,
      'DAY':cityDAY.value
  })
}
const saveMetric = async () => {
  const fields = {
    "NAME": metricName.value,
    "METRIC_ASUO": metricASUO.value,
    "MEANING": metricMeaning.value,
    "STANDART": metricStandart.value,
    "DESCRIPTION": metricDescription.value,
    "TESTS": metricTests.value
  }
  const res = await as.setMetric(fields)
  if (res > 0) {
    toast.add({severity: 'success', summary: 'Метрика успешно добавлена', detail: '', life: 3000});
  } else {
    toast.add({severity: 'error', summary: 'Неудалось добавить метрику', detail: '', life: 3000});
  }
}
const onRowEditSave = async (event) => {
  let {newData, index} = event;

  asr.metric.value[index] = newData;

  const result = await as.updateMetric(newData)
  if (result) {
    toast.add({severity: 'success', summary: 'Метрика успешно добавлена', detail: '', life: 3000});
  } else {
    toast.add({severity: 'error', summary: 'Неудалось добавить метрику', detail: '', life: 3000});
  }
};
const delMetric = ()=>{
  selectedProduct.value.forEach(async elem=>{
        const res = await as.deleteMetric(elem)
          if(res){
            toast.add({severity: 'success', summary: 'Метрика успешно удалена', detail: '', life: 3000});
          } else {
            toast.add({severity: 'error', summary: 'Неудалось удалить метрику', detail: '', life: 3000});
          }
  })
}

const saveChangeCity = async  (event)=>{
   asr.listCity.value =  event.value.ID
   await as.getCities()
}
const loadSmartFields = async  (event)=>{
   asr.settings.value.smartPersonalAccount =  event.value
   await as.getSmartFields()
}



const saveSettings = async ()=>{
        const checkSetting = await new Promise((resolve)=>{
          BX.rest.callMethod('mwsproactiverest.setting.getList',{
            filter:{
              "NAME":"PROACTIVE"
            }
          },res=>{
            resolve(res.data());
          })
        })

        if(checkSetting.length >0) {

          const add = await new Promise((resolve) => {
            BX.rest.callMethod('mwsproactiverest.setting.add', {
              "NAME": "PROACTIVE",
              'SETTING_STRING': asr.settings.value
            })
          })
        }else{
          const update = await new Promise((resolve) => {
            BX.rest.callMethod('mwsproactiverest.setting.update', {
              'ID':checkSetting[0].ID,
              'SETTING_STRING': asr.settings.value
            })
          })

        }


}


onMounted(async () => {
  await as.getDealCategory()
  await as.getDealFields()
  await as.getSmarts()
  await as.getLists()
  await as.getCities()
  await as.getMetric()

})

</script>

<style scoped>
.word-wrap {
  height: 38px;
  word-wrap: break-word;
  overflow: hidden;
}
</style>